import { Form } from './common/Form';
import { ensureElement } from '../utils/utils';
export class Address extends Form {
    constructor(container, events) {
        super(container, events);
        this._card = ensureElement('button[name=card]', this.container);
        this._cash = ensureElement('button[name=cash]', this.container);
        this._payment = ensureElement('.order__buttons', this.container);
        this._card.addEventListener('click', () => {
            this.events.emit(`cardpay:change`);
        });
        this._cash.addEventListener('click', () => {
            this.events.emit(`cashpay:change`);
        });
    }
    toogleButtonCardpay() {
        if (this._cash.classList.contains(`button_alt-active`)) {
            this.toggleClass(this._cash, `button_alt-active`);
        }
        this.toggleClass(this._card, `button_alt-active`);
    }
    toggleButtonCashpay() {
        if (this._card.classList.contains(`button_alt-active`)) {
            this.toggleClass(this._card, `button_alt-active`);
        }
        this.toggleClass(this._cash, `button_alt-active`);
    }
    get payment() {
        if (this._card.classList.contains(`button_alt-active`)) {
            return 'card';
        }
        if (this._cash.classList.contains(`button_alt-active`)) {
            return 'cash';
        }
    }
    set address(value) {
        this.container.elements.namedItem('address').value =
            value;
    }
}
export class Contacts extends Form {
    constructor(container, events) {
        super(container, events);
    }
    set phone(value) {
        this.container.elements.namedItem('phone').value =
            value;
    }
    set email(value) {
        this.container.elements.namedItem('email').value =
            value;
    }
}
